  margin-top: 32px;
  grid-template-columns: 1fr;
  gap: 30px;
  grid-template-areas: "name" "tel" "textarea" "button" "check"; }
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .modal-call__form {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; } }

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .modal-call__form input[type="text"],
  .modal-call__form input[type="tel"] {
    box-sizing: border-box;
    width: 100%; }
    .modal-call__form input[type="text"]:nth-child(n+2),
    .modal-call__form input[type="tel"]:nth-child(n+2) {
      margin-top: 30px; } }

.modal-call__form input[type="text"],
.modal-call__form input[type="tel"],
.modal-call__form textarea {
  padding: 14px 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.1);
  border: none; }
  .modal-call__form input[type="text"]::placeholder,
  .modal-call__form input[type="tel"]::placeholder,
  .modal-call__form textarea::placeholder {
    color: #ffffff; }
  .modal-call__form input[type="text"]:hover,
  .modal-call__form input[type="tel"]:hover,
  .modal-call__form textarea:hover {
    box-shadow: 0 0 20px 0 #ffffff;
    transition: 0.3s; }
  .modal-call__form input[type="text"]:active,
  .modal-call__form input[type="tel"]:active,
  .modal-call__form textarea:active {
    opacity: 0.6; }

.modal-call__form textarea {
  box-sizing: border-box;
  resize: none;
  grid-area: textarea;
  height: 104px; }
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .modal-call__form textarea {
      width: 100%;
      margin-top: 30px; } }

.modal-call__form label[for="modal-confirm"] {
  position: relative;
  grid-area: check;
  cursor: pointer;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  align-self: center;
  padding-left: 42px; }
  .modal-call__form label[for="modal-confirm"]::before {
    position: absolute;
    top: 0;
    left: 10px;
    content: "";
    width: 16px;
    height: 16px;
    border: 1px solid #ffffff; }
  @media (max-width: 767px) {
    .modal-call__form label[for="modal-confirm"] {
      padding-left: 32px; }
      .modal-call__form label[for="modal-confirm"]::before {
        left: 0; } }
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .modal-call__form label[for="modal-confirm"] {
      box-sizing: border-box;
      order: 2;
      width: 100%;
      margin-top: 30px; } }

.modal-call__form input[type="checkbox"]:checked + label::after {
  position: absolute;
  top: 3px;
  left: 16px;
  content: "";
  width: 5px;
  height: 8px;
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform-origin: center;
  transform: rotate(45deg); }
  @media (max-width: 767px) {
    .modal-call__form input[type="checkbox"]:checked + label::after {
      left: 6px; } }

.modal-call__form input[type="checkbox"]:focus + label {
  outline: 2px auto black;
  outline-offset: 5px; }

.modal-call__form button {
  grid-area: button;
  padding: 14px 16px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  font-family: "Roboto Condensed-Bold", "Arial", sans-serif;
  font-size: 14px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .modal-call__form button:hover {
    border-color: #00b2ff;
    background-color: #ffffff;
    color: #00b2ff;
    transition: 0.3s; }
  .modal-call__form button:active {
    opacity: 0.6; }
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    .modal-call__form button {
      box-sizing: border-box;
      width: 100%;
      margin-top: 30px; } }

.modal-call__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 16px;
  height: 16px;
  font-size: 0;
  border: none;
  background-color: transparent;
  background-image: url("../img/cross.svg");
  background-size: contain;
  cursor: pointer; }

.modal-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0.5;
  cursor: pointer; }
